











import { eventHub, SnackbarEvent } from "@/js/eventHub";
import Vue from "vue";

export default Vue.extend({
	data() {
		return {
			show: false,
			message: "",
			timeout: -1,
			color: "primary",
		};
	},
	created() {
		eventHub.$on(SnackbarEvent.ID, (e: SnackbarEvent) => {
			this.message = e.message;
			this.timeout = e.timeout;
			this.color = e.color;
			this.show = true;
		});
	},
	beforeDestroy() {
		eventHub.$off(SnackbarEvent.ID);
	},
});
