











import Vue from "vue";
import CardOptions from "./CardOptions.vue";

export default Vue.extend({
	components: {
		CardOptions
	},
	props: {
		outlined: Boolean,
		withOptions: Boolean
	},
	data() {
		return {
			hover: false,
		};
	},
	computed: {
		elevation(): number {
			let elevation = this.outlined ? 0 : 1;
			if (this.hover) elevation++;

			return elevation;
		},
	},
});
